import React, { useState } from 'react';

function Form4(props) {

  //    
  const [formData, setFormData] = useState({
    name: '',
    id_txt: '',
  });

  // input  change 
  const nameChange = (e) => {
    const { name, value } = e.target;

    setFormData((prev) => ({
      ...prev,
      [name]: value
    }));
  };

  //  
  const onDelete = () => {
    setFormData({
      name: '',
      id_txt: ''
    });
  };

  return (
    <>
      <h4 className="title4">4.  input   </h4>
      <ul>
        <li>e.target.value : (value) </li>
        <li>e.target.name : name  </li>
        <li>state   </li>
        <li>onChange    </li>
        <li>useState    </li>
      </ul>

      <form>
        <p>() :</p>
        <input
          type="text"
          name="name"
          value={formData.name}
          onChange={nameChange}
        />

        <p>() :</p>
        <input
          type="text"
          name="id_txt"
          value={formData.id_txt}
          onChange={nameChange}
        />
      </form>

      <p>
          '{formData.name}',
         '{formData.id_txt}'.
      </p>

      <input type="button" value="" onClick={onDelete} />
    </>
  );
}

export default Form4;