import React, {useState} from 'react';



// rsc   
const Count2 = () => {

  //   useState
  //  
  //const [, ] = useState();
  const[num,setNum] = useState(1);//

  //1   
  const onPlus1 = () =>{
    setNum(num+1);// 1  
  }

  // 1   
  const onMinus1=()=>{
    if(num =='1'){ //num 1  
      //1 
      setNum(1);
    }else{//   
    setNum(num-1); //1 
  }
  }

  return (
    <>
        <p>+, -   , state </p>
        <div>
          <input type="button" value="-" className='btn' onClick={onMinus1}/>
           : <span className='num'>
            {num}
          </span>
          <input type="button" value="+" className='btn' onClick={onPlus1}/>
        </div>
    </>
  );
};

export default Count2;