import React, { useState } from 'react';
import{FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import{faBell} from '@fortawesome/free-regular-svg-icons';
import {faHeart} from '@fortawesome/free-regular-svg-icons';
function Market(props) {

  const BASE_PRICE = 14320;

  const [num2, setNum] = useState(1);
  const [price, setPrice] = useState(BASE_PRICE);
  const [isActive, setIsActive] = useState(false);

  const onPlus2 = () => {
    const newNum = num2 + 1;
    setNum(newNum);
    setPrice(BASE_PRICE * newNum);
    setIsActive(true);
  };

  const onMinus2 = () => {
    if (num2 === 1) {
      setNum(1);
      setPrice(BASE_PRICE);
      setIsActive(false); 
    } else {
      const newNum = num2 - 1;
      setNum(newNum);
      setPrice(BASE_PRICE * newNum);
    }
  };

  return (
    <>
    <br />
      <p>[]      </p>
    <br />
      <div className='pricebox'>
      <div className="count">
        <input
          type="button"
          value="-"
          className={`btn2 ${isActive ? 'btn2act' : ''}`}
          onClick={onMinus2}
          />

        <span className="num2">{num2}</span>

        <input
          type="button"
          value="+"
          className="btn3"
          onClick={onPlus2}
          />
      </div>
      <div className="price2"><span><s>17900</s>14320</span></div>
      </div>
      <div className='result'>
      <p>
         :
        <span className="price"> {price.toLocaleString()}</span>
      </p>
      </div>
      <div className="btns">
        <button className='heart'>
          <FontAwesomeIcon icon={faHeart} style={{fontSize:'24px'}}/>
        </button>
        <button className='bell'>
          <FontAwesomeIcon icon={faBell} style={{fontSize:'24px'}}/>
        </button>
        <button className='btn4'> </button>
      </div>
    </>
  );
}

export default Market;