import React, { useState } from 'react';

function Form1(props) {
  // 
  //[, ] = useState(''); //state
  const [name, setName] = useState('');

  //       nameChange nameChange
  const nameChange =(e)=>{
    setName(e.target.value); //  .
  }
  //    nameReset
  const nameReset =()=>{
    setName('');//   ''  == 
  }
  return (
    <>
      <h4 className="title4">1.   </h4>
      <form action="">
        <p> .</p>
        <p>  : {name}</p>
        <input type="text" value={name} name='name' onChange={nameChange}/>
        <input type="button" value="" onClick={nameReset} />
        <ul>
          <li>input  'onChange'  (p)  (name)   </li>
          <li>html  DOM  </li>
          <li>React     </li>
          <li>     state .</li>
        </ul>
      </form>
    </>
  );
}

export default Form1;