import React from 'react';

function Child({name = '000', age =' 000', hobby ='000'}) {
  return (
    <>
      <div>
        <ul>
          <li></li>
          <li> : {name}</li>
          <li> : {age}</li>
          <li> : {hobby}</li>
        </ul>
      </div>
    </>
  );
}



export default Child;