import React, { Component } from 'react';

class Count1 extends Component {
  
  // 
  state={n:1}; // 1

  //   
  onPlus = () =>{ //1 
    console.log(' ');
    this.setState({
      n:this.state.n+1 // n 1  
    })
  }
  
  onMinus=()=>{ //1 
    console.log(' ');
    if(this.state.n===1){//  1 
      this.state.n=1;// 1 
    }else{//  
      this.setState({// -1  
      n:this.state.n-1 // n 1  
    });
    }
  }

  render() {
    return (
      <>
        <p>+, -   , state </p>
        <div>
          <input type="button" value="-" className='btn' onClick={this.onMinus}/>
           : <span className='num'>
            {this.state.n}
          </span>
          <input type="button" value="+" className='btn' onClick={this.onPlus}/>
        </div>
      </>
    );
  }
}

export default Count1;