import React,{useState} from 'react';

function Form2(props) {
  // 
  let header = '';
  //     
  const [name, setName] = useState('');
  const [pw_name,setPw] = useState('');
  

  //nameChange 
  const nameChange=(e)=>{//   
    setName(e.target.value);
  }
  const pwChange=(e)=>{//pw   
    setPw(e.target.value);
  }

  //      ,     .
  if(name){
    header=<p>   {name},  {pw_name} .</p>
  }else{
    header = '';
  }

  // onDataReset   name, pw_name  
  const onDataReset=(e)=>{
    setName('');
    setPw('');
  }

  return (
    <>
      <h4 className='title4'>2. if  </h4>
        {/* name, pw_name  */}
        {header}

      <form action="">
        <label htmlFor="id_name">ID : </label>
        <input type="text" id='id_name' name='id_name' value={name} onChange={nameChange} />

        <label htmlFor="pw_name">PW :</label>
        <input type="text" id='pw_name' name='pw_name' value={pw_name} onChange={pwChange}/>
      </form>
      <p>
        <button type='button' onClick={onDataReset}>  </button>
      </p>
    </>
  );
}

export default Form2;